/*
**目录样式**
*/
.catalog {
  position: relative;
}
.catalog .container {
  max-width: 700px;
  margin: 0 auto;
}
.catalog .container .title h1 {
  text-align: center;
  color: #262626;
  font-size: 24px;
}
.catalog .container .contents {
  position: relative;
  padding-top: 40px;
  min-height: 320px;
}
.catalog .container .contents ul {
  margin-bottom: 0;
}
.catalog .container .contents ul .catalog-item-1 {
  margin-left: 0;
  font-weight: 600;
  height: 42px;
  line-height: 42px;
  font-size: 15px;
  list-style: disc !important;
}
.catalog .container .contents ul .catalog-item-nochild {
  cursor: pointer;
}
.catalog .container .contents ul .catalog-item-nochild:hover {
  color: #1890ff;
}
.catalog .container .contents ul .catalog-item-2 {
  margin-left: 16px;
  list-style: none;
}
.catalog .container .contents ul li {
  line-height: 18px;
  height: 36px;
  line-height: 36px;
}
.catalog .container .contents ul li a {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  background: #fff;
  color: #595959;
  padding: 0;
}
.catalog .container .contents ul li a:hover {
  color: #1890ff;
}
